import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return {
            username: '',
            password: ''
        }
    },

    computed: {
        ...mapGetters([
            'user'
        ])
    },

    methods: {
        ...mapActions([
            'login'
        ]),
        onLogin() {
            this.login({ username: this.username,password: this.password})
            .then(()=>{
                this.user && this.$router.push({path: this.$route.query.redirect || '/'})
            })
        }
    }
}


